import { useState } from 'react'
import { useSelector, useDispatch } from 'react-redux'
import { contactsOperations, contactsSelectors } from 'redux/contacts'
import { Form, Label, Input, Button } from './ContactForm.styles'
import { GiButterflyWarning } from 'react-icons/gi'
import toast from 'react-hot-toast'
import SpinnerTwo from 'components/SpinnerTwo'

function ContactForm() {
  const [name, setName] = useState('')
  const [number, setNumber] = useState('')
  const items = useSelector(contactsSelectors.getItems)
  const dispatch = useDispatch()
  const isLoading = useSelector(contactsSelectors.getIsLoading)

  const handleInputChange = (e) => {
    const { name, value } = e.currentTarget

    switch (name) {
      case 'name':
        return setName(value)

      case 'number':
        return setNumber(value)

      default:
        throw new Error(`there is no such name as ${name}`)
    }
  }

  const resetFormInputs = () => {
    setName('')
    setNumber('')
  }

  const showAlert = (value) => {
    toast(`${value} is already in contacts`, {
      style: { color: '#456173' },
      icon: <GiButterflyWarning />,
    })
  }

  const handleSubmit = async (e) => {
    e.preventDefault()
    const normalizedName = name.toLowerCase()
    const contactExists = items.find(
      ({ name }) => name.toLowerCase() === normalizedName,
    )
    const numberExists = items.find((contact) => contact.number === number)

    if (contactExists) {
      showAlert(name)
      resetFormInputs()
      return
    }

    if (numberExists) {
      showAlert(number)
      resetFormInputs()
      return
    }

    dispatch(contactsOperations.fetchAddContact({ name, number }))
    resetFormInputs()
  }

  return (
    <Form onSubmit={handleSubmit}>
      <>
        <Label>
          Name
          <Input
            onChange={handleInputChange}
            type="text"
            name="name"
            autoComplete="off"
            value={name}
            pattern="^[a-zA-Z–∞-—è–ê-–Ø]+(([' -][a-zA-Z–∞-—è–ê-–Ø ])?[a-zA-Z–∞-—è–ê-–Ø]*)*$"
            title="–ò–º—è –º–æ–∂–µ—Ç —Å–æ—Å—Ç–æ—è—Ç—å —Ç–æ–ª—å–∫–æ –∏–∑ –±—É–∫–≤, –∞–ø–æ—Å—Ç—Ä–æ—Ñ–∞, —Ç–∏—Ä–µ –∏ –ø—Ä–æ–±–µ–ª–æ–≤. –ù–∞–ø—Ä–∏–º–µ—Ä Adrian, Jacob Mercer, Charles de Batz de Castelmore d'Artagnan –∏ —Ç. –ø."
            required
          />
        </Label>
        <Label>
          Number
          <Input
            onChange={handleInputChange}
            type="tel"
            name="number"
            autoComplete="off"
            value={number}
            pattern="\+?\d{1,4}?[-.\s]?\(?\d{1,3}?\)?[-.\s]?\d{1,4}[-.\s]?\d{1,4}[-.\s]?\d{1,9}"
            title="–ù–æ–º–µ—Ä —Ç–µ–ª–µ—Ñ–æ–Ω–∞ –¥–æ–ª–∂–µ–Ω —Å–æ—Å—Ç–æ—è—Ç—å —Ü–∏—Ñ—Ä –∏ –º–æ–∂–µ—Ç —Å–æ–¥–µ—Ä–∂–∞—Ç—å –ø—Ä–æ–±–µ–ª—ã, —Ç–∏—Ä–µ, –∫—Ä—É–≥–ª—ã–µ —Å–∫–æ–±–∫–∏ –∏ –º–æ–∂–µ—Ç –Ω–∞—á–∏–Ω–∞—Ç—å—Å—è —Å +"
            required
          />
        </Label>
        <Button type="submit" disabled={isLoading}>
          {isLoading ? (
            <>
              <SpinnerTwo size={18} />
              Adding...
            </>
          ) : (
            'Add contact'
          )}
        </Button>
      </>
    </Form>
  )
}

export default ContactForm
